import { Component, Vue } from 'vue-property-decorator'
import { getTransDetail } from '@/api/sepAccount/sepAccount'
@Component({
  components: {},
})
export default class SepAccountDetail extends Vue {
  private pageTitle = '分账账户详情'
  // 分账方id
  private id = ''
  // 该分账方信息
  private data: any = {}
  public mounted(): void {
    this.id = Vue.ls.get('/manage/sys/SepAccountDetail')
    this.getTrans()
  }
  /**
   * @Author roct
   * @Description 获取分账方详情
   * @Date 22:18 2021/5/26 0026
   **/
  private async getTrans() {
    if (!this.id) {
      return
    }
    const res = await getTransDetail({ id: this.id })
    this.data = res.data
  }
}
